<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">Create Sub-category</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
        <router-link to="/admin/subcategories">
          <v-btn color="error">
            <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
        </router-link>
      </div>
    </div>
    <template>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-row>
          <v-col cols="12" sm="6" md="12" class="py-0">
            <v-text-field 
                  v-model="formData.subcat_name"
                  label="Sub category Name"></v-text-field>
          </v-col>
          <v-col cols="12" sm="6" md="12" class="py-0">
              <v-autocomplete
                label="Select category"
                v-model="formData.cat_id"
                :items="mainCatSelect"
                item-text="cat_name"
                item-value="id">
              </v-autocomplete>
          </v-col>
          <v-col cols="12" sm="6" md="12" class="py-0">
            <v-file-input
              v-model="formData.selectedFile"
              :rules="filesRules"
              label="Category Image"
              accept="image/*"
              @change="handleFileChange"
              v-on:click="isHidden = true"
          ></v-file-input>
          <v-img v-if="previewImage" :src="previewImage" class="img-fluid" width="100" height="100"></v-img>
            
          </v-col>
          <v-col cols="12" sm="6" md="12">
            <v-select
                v-model="formData.subcat_feature"
                label="Select Featured"
                :items="feature"
                item-value="status"
                item-text="feature"
            ></v-select>
          </v-col>
          <v-col cols="12" sm="6" md="12" class="py-0 d-flex" style="justify-content: space-between;">
              <div></div>
              <div class="ms-3 pb-3">
                <v-btn class="my-4 me-3 cred" @click="reset">CANCEL</v-btn>
                <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                <v-btn v-else @click="AddSubCategory" class="my-4 me-3 cred">SAVE</v-btn>
              </div>
          </v-col>
        </v-row>
      </v-form>
    </template>
    <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
      <template v-slot:action="{}">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import axios from 'axios';
export default {
    name: "SubAdmins",
    components: {  },
    data: () => ({
        valid: true,
        readers: [],
        mainCatSelect: [],
        feature:[{status:1, feature:'Popular'}, {status:0, feature:'Not Popular'}],
        formData: {
          subcat_name: '',
          cat_id: '',
          subcat_feature: '',
          subcat_status: '',
          selectedFile: ''
        },
        isHidden: false,
        snackbar: false,
        text: '',
        timeout: '',
        isLoading: false,
    }),
    created() {
      this.mainCategoriesForSelect();
    },
    methods: {
      reset(){
        this.previewImage = null;
        this.$refs.form.reset();
      },
      handleFileChange() {
        this.previewImage = URL.createObjectURL(this.formData.selectedFile);
      },
    
      mainCategoriesForSelect() {
          axios.get("admin/allcat/drop")
          .then((resp) =>{
              this.mainCatSelect = resp.data.maincategories_drop;
          });
      },
      AddSubCategory(){
        this.isLoading = true;
          const config = {
              headers: { 'content-type': 'multipart/form-data' }
          }
          let data = { 
              subcat_name: this.formData.subcat_name,
              cat_id: this.formData.cat_id,
              subcat_feature: this.formData.subcat_feature,
              subcat_status: 1,
              new_subCat_img_url : this.formData.selectedFile,
          };
          if(this.formData.subcat_name!='' && this.formData.cat_id!=='' && this.formData.selectedFile){
            axios.post("admin/subcategory/add", data, config)
              .then((resp) =>{
                  this.isLoading = false;
                    this.snackbar = true;
                    this.text = resp.data.message;
                    this.timeout = 3000;
                    this.previewImage = null;
                    this.$refs.form.reset();
              });
          }else{
            this.snackbar = true;
            this.text = "Please fill all inputs.";
            this.timeout = 3000;
            this.isLoading = false;
          }   
      } 
    },
}
</script>